                  <input type="text" class="form-control text-right w-100" v-model.number="values.exchangeFee" @input="onInput" @keydown="onInput" @blur="onInput">
                </div>
              </td>
              <td>
                {{ customFixed(values.fx) }}
              </td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ values.baseAsset }}</span>
                  </div>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.blockchainFee" @input="onInput" @keydown="onInput" @blur="onInput">
                </div>
              </td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ values.baseAsset }}</span>
                  </div>
                  <div class="form-control d-flex justify-content-end w-100">{{ values.after }}</div>
                  <div class="input-group-addon bg-light">
                    <span v-if="values.check" style="color: transparent; text-shadow: 0 0 0 green;">&#10004;</span>
                    <span v-if="!values.check" style="color: transparent; text-shadow: 0 0 0 red;">&#10006;</span>
                  </div>
                </div>
              </td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ values.baseAsset }}</span>
                  </div>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.coldStorage" @input="onInput" @keydown="onInput" @blur="onInput">
                  <div class="input-group-addon bg-light">
                    <span v-if="values.csCheck" style="color: transparent; text-shadow: 0 0 0 green;">&#10004;</span>
                    <span v-if="!values.csCheck" style="color: transparent; text-shadow: 0 0 0 red;">&#10006;</span>
                  </div>
                </div>
              </td>
            </template>
          </tr>
        </tbody>
      </table>
    </div>

    <!--TABLES-->
    <div class="list-group-item row mt-5" v-for="(values, index) in getClients">
      <div class="row pt-3">
        <div class="col d-flex justify-content-start">{{ values.fullName }}</div>
        <div class="col d-flex justify-content-end">
          {{ customFixed(values.details.baseAssetPieces) }} {{ values.details.assets[baseAsset.ticker].ticker }}
        </div>
        <div class="col d-flex justify-content-end">
          {{ parseFloat(values.details.percentage * 100).toFixed(4) }}%
        </div>
      </div>
      <table class="table text-center table-striped">
        <thead>
          <tr>
            <th scope="col">Asset</th>
            <th scope="col">Before</th>
            <th scope="col">CCY1 Amount</th>
            <th scope="col">CCY2 Amount</th>
            <th scope="col">FX</th>
            <th scope="col">B Fee</th>
            <th scope="col">After</th>
            <th scope="col">Check</th>
          </tr>
        </thead>
        <tbody>
          <!--FIRST ROW-->
          <tr>
            <th scope="row">{{ tableValues[0].baseAsset }}</th>
            <td>{{ customFixed(values.details.baseAssetPieces) }}</td>
            <td>N/A</td>
            <td>
              <span v-if="values.details.ccy2" style="color: transparent; text-shadow: 0 0 0 green;">&#10004;</span>
              <span v-if="!values.details.ccy2" style="color: transparent; text-shadow: 0 0 0 red;">&#10006;</span>
            </td>
