<template>
  <div class="container-fluid">

    <div class="row py-3">
      <div class="col-md-12">
        <h1 class="text-center">Client details</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-md-12">
        <client-table :client="client" :groups="groups" v-if="client"></client-table>
      </div>

      <div class="col-md-12" v-if="hasPortfolio">
        <div class="card portfolio-card">
          <div class="card-header">
            <h2>Portfolio #{{ client.portfolios[0] }}: {{ portfolioStats ? portfolioStats.totalValueBGN : null }} BGN</h2>
          </div>
          <div class="card-body">
            <b-tabs v-model="tabIndex">

              <!-- TAB #1 -->
              <b-tab title="Overview" active class="pt-5">
                <div v-if="portfolioAssets && portfolioAssets.length" class="mb-5">
                  <pie-chart :assets="portfolioAssets"></pie-chart>
                  <assets-table :coins="portfolioAssets"></assets-table>
                  <p>* The prices are taken from <a href="https://coinmarketcap.com/">https://coinmarketcap.com</a> on {{ updateTime }} UTC.</p>
                </div>

                <h3>Return on Investment (BGN)</h3>
                <totals-table v-if="portfolioStats" :data="portfolioStats"></totals-table>
                <div v-if="!portfolioStats" class="alert text-center">No Data</div>
              </b-tab>

              <!-- TAB #2 -->
              <b-tab title="Transactions" class="pt-5">
                <transactions-table v-if="tabIndex === 1" :portfolioId="client.portfolios[0]"></transactions-table>
              </b-tab>

            </b-tabs>
          </div>
        </div>
      </div>

      <div class="col-md-12 mt-5">
        <button type="button" class="btn btn-warning" @click="resetPass()">Reset Password</button>
        <!-- <button type="button" class="btn btn-danger" @click="deactivate()">Deactivate</button> -->
      </div>

      <loading :loading="loading"></loading>

    </div>

  </div>
</template>


<script>
import userService from '../services/userService'

import AssetsTable from '../components/tables/AssetsTable'
import ClientTable from '../components/tables/ClientTable'
import TotalsTable from '../components/tables/PortfolioTotalsTable'
import TransactionsTable from '../components/tables/TransactionsTable'
import PieChart from '../components/charts/AssetsPieChart'

export default {
  components: {
    ClientTable,
    TotalsTable,
    TransactionsTable,
    AssetsTable,
    PieChart,
  },
  created() {
    const clients = this.$store.getters.getClients()
    let client
    let clientPromise
    let groupPromise

    this.loading = true
    groupPromise = this.$store.dispatch('loadGroups')

    if (clients && clients.length) {
      client = clients.find(cl => cl.id === this.clientId)
    }

    if (!client) {
      client = this.$store.getters.getClient
    }

    if (!client || client.id !== this.clientId) {
      // We have to fetch the client
      clientPromise = this.$store.dispatch('getClient', this.clientId)
    } else {
      // We alrady have the client
      clientPromise = this.$store.dispatch('setClient', client)
    }

    clientPromise
      .then(client => client.portfolios)
      .then(portfolioIds => {
        if (!portfolioIds || !portfolioIds.length) {
          return Promise.reject(new Error('The client does not have any portfolio.'))
        }
        return this.$store.dispatch('loadPortfolioDetails', portfolioIds[0])
      })
      .then(() => {
        this.hasPortfolio = true
      })
      .catch(() => {
        this.hasPortfolio = false
      })

    Promise.all([clientPromise, groupPromise])
      .finally(() => {
        this.loading = false
      })
  },
  props: ['id'],
  data() {
    return {
      loading: false,
      tabIndex: null,
      hasPortfolio: null,
    }
  },
  methods: {
    resetPass() {
      if (window.confirm(`Are you sure you want to reset the password of ${this.client.name} with Client ID: ${this.client.clientId}? If 'YES', the user will receive an email with a link to set a new password.`)) {
        userService.sendMailForNewPass(this.client.email)
      }
    },
    deactivate() {
      if (window.confirm(`Are you sure you want to deactivate user ${this.client.name} with Client ID: ${this.client.id}?`)) {
        userService.deactivate(this.client.email)
      }
    },
  },
  computed: {
    clientId() {
      return +this.id
    },
    client() {
      return this.$store.getters.getClient
    },
    portfolioStats() {
      return this.$store.getters.getSPStats
    },
    portfolioAssets() {
      return this.$store.getters.getSPAssets
    },
    updateTime() {
      return this.$store.getters.getUpdateTime
    },
    groups() {
      return this.$store.getters.getGroups
    },
  },
}
</script>



