import Vue from 'vue'
import Vuex from 'vuex'
import groups from './modules/groupsStore'
import portfolio from './modules/portfolioStore'
import upload from './modules/uploadStore'
import users from './modules/usersStore'

Vue.use(Vuex)

export const store = new Vuex.Store({
  modules: {
    groups,
    portfolio,
    upload,
    users,
  },
})



